:host {
    --primary: #4e54c8;
    --primary-light: #8f94fb;
    --primary-dark: #363795;
    --accent: #4caf50;
    --accent-light: #81c784;
    --text-primary: #333;
    --text-secondary: #666;
    --background-light: #f5f7fa;
    --card-bg: #ffffff;
    --success: #00c853;
    --warning: #ffd600;
    --danger: #f44336;
    --normal: #2196f3;
  }

  .system-monitoring-container {
    padding: 24px;
    background-color: var(--background-light);
    min-height: 100%;
  }

  .toolbar {
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
    background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
    color: white;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
    margin-bottom: 24px;
  }

  .toolbar-left {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  .toolbar-right {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  .app-title {
    font-size: 1.25rem;
    font-weight: 600;
    letter-spacing: 0.5px;
  }

  .metrics-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 24px;
  }

  .metric-card {
    background-color: var(--card-bg);
    border-radius: 8px;
    overflow: hidden;
  }

  .metric-header {
    display: flex;
    align-items: center;
    padding: 16px;
  }

  .metric-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 12px;
    color: white;
  }

  .metric-icon.normal {
    background-color: var(--normal);
  }

  .metric-icon.warning {
    background-color: var(--warning);
  }

  .metric-icon.critical {
    background-color: var(--danger);
  }

  .metric-icon i {
    font-size: 18px;
  }

  .metric-title h3 {
    margin: 0;
    font-size: 1rem;
    font-weight: 600;
    color: var(--text-primary);
  }

  .metric-title p {
    margin: 0;
    font-size: 0.75rem;
    color: var(--text-secondary);
  }

  .metric-value {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
  }

  .metric-value .value {
    font-size: 2rem;
    font-weight: 700;
    color: var(--text-primary);
  }

  .metric-value .trend {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 0.875rem;
    font-weight: 500;
  }

  .trend-up {
    color: var(--success);
  }

  .trend-down {
    color: var(--danger);
  }

  .trend-stable {
    color: var(--warning);
  }

  .metric-progress {
    height: 8px;
    border-radius: 4px;
    margin: 0 16px;
  }

  .metric-status {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
  }

  .status-badge {
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 0.75rem;
    font-weight: 500;
    text-transform: uppercase;
  }

  .status-badge.normal {
    background-color: rgba(33, 150, 243, 0.1);
    color: var(--normal);
  }

  .status-badge.warning {
    background-color: rgba(255, 214, 0, 0.1);
    color: var(--warning);
  }

  .status-badge.critical {
    background-color: rgba(244, 67, 54, 0.1);
    color: var(--danger);
  }

  .last-updated {
    font-size: 0.75rem;
    color: var(--text-secondary);
    display: flex;
    align-items: center;
    gap: 4px;
  }

  .last-updated i {
    font-size: 14px;
  }

  mat-card-actions {
    display: flex;
    justify-content: space-between;
    padding: 8px 16px;
  }

  mat-card-actions button {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 0.875rem;
  }

  @media (max-width: 768px) {
    .metrics-grid {
      grid-template-columns: 1fr;
    }

    .toolbar {
      flex-direction: column;
      height: auto;
      padding: 12px;
    }

    .toolbar-left, .toolbar-right {
      width: 100%;
      justify-content: center;
      margin: 4px 0;
    }

    .toolbar-right {
      margin-top: 8px;
    }
  }

  @media (max-width: 480px) {
    .system-monitoring-container {
      padding: 16px 8px;
    }

    .metric-card {
      border-radius: 0;
    }
  }